import BaseMapper from "src/viz//base-mapper";

export default class MapperVizReal extends BaseMapper {
  map(dnFeatures, params) {
    const mapped = {};
    for (let i = 0; i < params.length; i++) {
      mapped[params[i]] = dnFeatures[i];
    }
    mapped.sphRad *= 40;
    mapped.sphPos *= 300;
    mapped.camFreq /= 3;
    mapped.camHeight = (mapped.camHeight - 0.5) * 400;
    return mapped;
  }
}
