import THREE from "src/three";

export function numToColor(val) {
  let color = Math.floor(val * (256 ** 3));
  let colR = Math.floor(color / (256 ** 2));
  color = color % (256 ** 2);
  let colG = Math.floor(color / (256));
  color = color % 256;
  let colB = color;
  return new THREE.Color(colR, colG, colB);
}
